<template>
	<view>
		<view class="search-box">
			<uni-search-bar @confirm="search" @input="input" cancelButton="none" :radius="100"></uni-search-bar>
		</view>


		<!-- 搜索建议类表 -->
		<view class="sugg-list" v-if="searchResults.length !== 0">
			<view class="sugg-item" v-for="(item,index) in searchResults" :key="index" @click="gotoDetail(item)">
				<view class="goods-name">{{item.goods_name}}</view>
				<uni-icons type="arrowright" size="16"></uni-icons>
			</view>
		</view>

		<!-- 历史记录 -->
		<view class="history-box" v-else>
			<!-- 标题区域 -->
			<view class="history-title">
				<text>历史记录</text>
				<uni-icons type="trash" size="17" @click="clean"></uni-icons>
			</view>
			<!-- 列表区域 -->
			<view class="history-list">
				<uni-tag :text="item1" v-for="(item1,index1) in histories" :key="index1" @click="gotoGoodsList(item1)"></uni-tag>
			</view>
		</view>


	</view>


</template>

<script>
	export default {
		data() {
			return {
				timer: null,
				kw: '',
				// 搜索的结果
				searchResults: [],
				// 搜索历史记录
				historyList: []
			};
		},
		onLoad() {
			// 获取保存在本地的值  转成数组JSON.parse
			this.historyList = JSON.parse(uni.getStorageSync('kw') || '[]')
		},
		methods: {
			input(e) {
				// 防抖
				clearTimeout(this.timer)
				this.timer = setTimeout(() => {
					this.kw = e.value
					this.getSearchList()
				}, 1000)
			},
			async getSearchList() {
				// 判断搜索关键词是否为空
				if (this.kw.length === 0) {
					this.searchResults = []
					return
				}
				const {
					data: res
				} = await uni.$http.get('/api/public/v1/goods/qsearch', {
					query: this.kw
				})
				if (res.meta.status !== 200) return uni.$showMsg()
				this.searchResults = res.message

				this.saveSearchHistory()
			},
			gotoDetail(item) {
				uni.navigateTo({
					url: '/subpkg/goods_detail/goods_detail?goods_id=' + item.goods_id
				})
			},
			saveSearchHistory() {
				// this.historyList.push(this.kw)
				const set = new Set(this.historyList)
				set.delete(this.kw)
				set.add(this.kw)
				this.historyList = Array.from(set)
				// 调用uni.setStorageSync(key,value) 永久将数据(历史)保存在本地 转成字符串JSON.stringify
				uni.setStorageSync('kw', JSON.stringify(this.historyList))
			},
			clean(){
				// 清空
				this.historyList = []
				uni.setStorageSync('kw',[])
			},
			gotoGoodsList(item1){
				uni.navigateTo({
					url:'/subpkg/goods_list/goods_list?query=' + item1
				})
			}
		},
		computed: {
			histories() {
				return [...this.historyList].reverse()
			}
		}
	}
</script>

<style lang="scss">
	.search-box {
		position: sticky;
		top: 0;
		z-index: 999;
	}

	.sugg-list {
		padding: 0 5px;

		.sugg-item {
			display: flex;
			align-items: center;
			justify-content: space-between;
			font-size: 12px;
			padding: 13px 0;
			border-bottom: 1px solid #EFEFEF;

			.goods-name {
				// 文字不允许换行
				white-space: nowrap;
				// 超出隐藏
				overflow: hidden;
				// 多出的用...替代
				text-overflow: ellipsis;
			}
		}
	}

	.history-box {
		padding: 0 5px;

		.history-title {
			display: flex;
			justify-content: space-between;
			height: 40px;
			align-items: center;
			font-size: 13px;
			border-bottom: 1px solid #efefef;
		}

		.history-list {
			display: flex;
			flex-wrap: wrap;

			.uni-tag {
				margin-top: 5px;
				margin-right: 5px;
			}
		}
	}
</style>
